export default {
  data: () => {
    return {
      video: {
        width: 560,
        height: 315
      }
    }
  },
  methods: {
    show() {
      this.$modal.show('video')
    }
  },
  beforeMount() {
    const screenWidth = screen.width
    if(screenWidth < 700) {
      this.video.width = 420
      this.video.height = 236
    }
    if(screenWidth < 475) {
      this.video.width = 320
      this.video.height = 180
    }
  }
}
